import React from 'react'
import './Results.css'
import { Bar } from 'react-chartjs-2';
import Impacts from '../Impacts/Impacts'
import Download from '../Download/Download'
import { roofTypeValue, foundationTypeValue } from '../../functions/simpleFunctions'

const BetonMatPas = require("../../materialepas/beton.json")
const GipsMatPas = require("../../materialepas/gips.json")
const IsoleringMatPas = require("../../materialepas/isolering.json")
const MetalMatPas = require("../../materialepas/metal.json")
const MurstenMatPas = require("../../materialepas/mursten.json")
const TagpapMatPas = require("../../materialepas/pap.json")
const SkiferMatPas = require("../../materialepas/skifer.json")
const StenMatPas = require("../../materialepas/sten.json")
const TeglMatPas = require("../../materialepas/tegl.json")
const TræMatPas = require("../../materialepas/træ.json")

class Results extends React.Component {
    constructor(props) {
        super(props)

        this.sumMaterials = this.sumMaterials.bind(this)
        this.removeMissing = this.removeMissing.bind(this)
        this.getLabels = this.getLabels.bind(this)
        this.getData = this.getData.bind(this)
        this.passMaterials = this.passMaterials.bind(this)
        this.materialPercentages = this.materialPercentages.bind(this)
    }

    sumMaterials(array) {
        const newArray = []
        const Beton = ['Beton',0]
        const Træ = ['Træ',0]
        const Skifer = ['Skifer',0]
        const Tegl = ['Tegl',0]
        const Isolering = ['Isolering',0]
        const Tagpap = ['Tagpap',0]
        const Mursten = ['Mursten',0]
        const Gips = ['Gips', 0]
        const Metal = ['Metal', 0]
        const Sten = ['Sten', 0]
        const Sand = ['Sand', 0]
        const Kalk = ['Kalk', 0]
        const Ler = ['Ler', 0]
        const Vand = ['Vand', 0]
        const Papir = ['Papir', 0]
        const Jern = ['Jern', 0]
        const Stål = ['Stål', 0]
        const Polyester = ['Polyester', 0]
        const Bitumen = ['Bitumen', 0]
        const Gummi = ['Gummi', 0]

        for (var x of array) {
            if (x[0] === 'Træ') {
                Træ[1] = Træ[1]+x[1]
            }
            if (x[0] === 'Beton') {
                Beton[1] = Beton[1]+x[1]
            }
            if (x[0] === 'Skifer') {
                Skifer[1] = Skifer[1]+x[1]
            }
            if (x[0] === 'Tegl') {
                Tegl[1] = Tegl[1]+x[1]
            }
            if (x[0] === 'Isolering') {
                Isolering[1] = Isolering[1]+x[1]
            }
            if (x[0] === 'Tagpap') {
                Tagpap[1] = Tagpap[1]+x[1]
            }
            if (x[0] === 'Mursten') {
                Mursten[1] = Mursten[1]+x[1]
            }
            if (x[0] === 'Gips') {
                Gips[1] = Gips[1]+x[1]
            }
            if (x[0] === 'Metal') {
                Metal[1] = Metal[1]+x[1]
            }
            if (x[0] === 'Sten') {
                Sten[1] = Sten[1]+x[1]
            }
            if (x[0] === 'Sand') {
                Sand[1] = Sand[1]+x[1]
            } 
            if (x[0] === 'Kalk') {
                Kalk[1] = Kalk[1]+x[1]
            } 
            if (x[0] === 'Ler') {
                Ler[1] = Ler[1]+x[1]
            }
            if (x[0] === 'Vand') {
                Vand[1] = Vand[1]+x[1]
            }
            if (x[0] === 'Papir') {
                Papir[1] = Papir[1]+x[1]
            }
            if (x[0] === 'Jern') {
                Jern[1] = Jern[1]+x[1]
            }
            if (x[0] === 'Stål') {
                Stål[1] = Stål[1]+x[1]
            }
            if (x[0] === 'Polyester') {
                Polyester[1] = Polyester[1]+x[1]
            }
            if (x[0] === 'Bitumen') {
                Bitumen[1] = Bitumen[1]+x[1]
            }          
            if (x[0] === 'Gummi') {
                Gummi[1] = Gummi[1]+x[1]
            }                   
        }
        newArray.push(Beton) 
        newArray.push(Træ)
        newArray.push(Skifer)
        newArray.push(Tegl)
        newArray.push(Isolering)
        newArray.push(Tagpap)
        newArray.push(Mursten)
        newArray.push(Metal)
        newArray.push(Gips)
        newArray.push(Sten)
        newArray.push(Sand)
        newArray.push(Kalk)
        newArray.push(Ler)
        newArray.push(Vand)
        newArray.push(Papir)
        newArray.push(Jern)
        newArray.push(Stål)
        newArray.push(Polyester)
        newArray.push(Bitumen)
        newArray.push(Gummi)
        return newArray
    }

    removeMissing(array) {
        const newArray = []
        for (var x of array) {
            if (x[1] !== 0) {
                newArray.push(x)
            }
        } return newArray
    }

    getLabels(array) {
        const newArray = []
        for (var x of array) {
            newArray.push(x[0])
        } return newArray
    }

    getData(array) {
        const newArray = []
        for (var x of array) {
            newArray.push(x[1])
        } return newArray
    }

    passMaterials(array) {
        const newArray = []
        const newArray2 = []
        for (var x of array) {
            if (eval(`${x[0]}`+'MatPas').Indhold['Komponent/materiale'] === x[0]) {
                newArray.push([x,eval(`${x[0]}`+'MatPas').Indhold['Komponent består af']])
            } 
        } 
        for (var x of newArray) {
            for (var y of x[1]) {
                newArray2.push([y[0], x[0][1]*y[1]])
            }
        }
        return newArray2
    }

    materialPercentages(array) {
        var newNum = 0
        var newArray = []
        for (var x of array) {
            newNum = newNum + x[1]
        }
        for (var x of array) {
            newArray.push([x[0],(x[1]/newNum)*100])
        } return newArray
    }


    render() {

         //-------------------------------------VARIABLES-----------------------------------------------
         //#region
         const area = this.props.building.area
         const floors = this.props.building.floors
         const totalArea = area*floors
         const perimeter = Math.floor(Math.sqrt(area)*4)
         const wallArea = perimeter*(floors*2.5)
         const roofArea = Math.floor(area*roofTypeValue(this.props.building.roofType))
         const foundationArea = area*foundationTypeValue(this.props.building.foundationType)
         const windowArea = Math.floor(wallArea*0.15)
         //#endregion
         
         //-----------------------------------MATERIAL AMOUNTS------------------------------------------
         //#region
         const wallMaterialAmount = this.props.materials.wall.map((item) => <li>{item[0]}: {(item[1]*wallArea).toFixed(2)}m3</li>)
         const wallMaterialAmount2 = this.props.materials.wall.map((item) => [item[0],item[1]*wallArea])

         const roofMaterialAmount = this.props.materials.roof.map((item) => <li>{item[0]}: {(item[1]*roofArea).toFixed(2)}m3</li>)
         const roofMaterialAmount2 = this.props.materials.roof.map((item) => [item[0],item[1]*roofArea])

         const floorMaterialAmount = this.props.materials.floor.map((item) => <li>{item[0]}: {(item[1]*totalArea).toFixed(2)}m3</li>)
         const floorMaterialAmount2 = this.props.materials.floor.map((item) => [item[0],item[1]*totalArea])

         const foundationMaterialAmount = this.props.materials.foundation.map((item) => <li>{item[0]}: {(item[1]*foundationArea).toFixed(2)}m3</li>)
         const foundationMaterialAmount2 = this.props.materials.foundation.map((item) => [item[0],item[1]*foundationArea])

         const inWallMaterialAmount = this.props.materials.inwall.map((item) => <li>{item[0]}: {(item[1]*wallArea*0.5).toFixed(2)}m3</li>)
         const inWallMaterialAmount2 = this.props.materials.inwall.map((item) => [item[0],item[1]*wallArea*0.5])

         const merged = [].concat.apply([], [wallMaterialAmount2,roofMaterialAmount2,floorMaterialAmount2,foundationMaterialAmount2,inWallMaterialAmount2]);
         const mergedMaterials = this.removeMissing(this.sumMaterials(merged))

         const mergedMaterialAmount = mergedMaterials.map((item) => <li>{item[0]}: {item[1].toFixed(2)}m3</li>)

         const mergedPassMaterial = this.removeMissing(this.sumMaterials(this.passMaterials(this.materialPercentages(mergedMaterials)))).sort(function(a,b){return b[1] - a[1]})
         const mergedPassMaterialAmount = mergedPassMaterial.map((item) => <li>{item[0]}: {item[1].toFixed(2)}%</li>)
         //#endregion

         //---------------------------------------CHART-------------------------------------------------
         //#region 
         const chartData = this.getData(mergedMaterials)
         const chartLabels = this.getLabels(mergedMaterials)

         const data = {
            labels: chartLabels,
            datasets: [
              {
                label: '',
                data: chartData,
                backgroundColor: [
                  'rgba(70, 200, 235, 1)',
                  'rgba(30, 132, 235, 1)',
                  'rgba(50, 170, 150, 1)',
                  'rgba(24, 200, 235, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(100, 162, 235, 1)',
                  'rgba(120, 152, 205, 1)',
                  'rgba(50, 255, 205, 1)',
                  'rgba(50, 105, 255, 1)',
                  'rgba(70, 125, 235, 1)',
                ],
              },
            ],
          }
          
          const options = {
            maintainAspectRatio: false, 
            legend: {
                display: false
            },
            scales: {
                xAxes: [{
                    gridLines: {
                        display:false
                    }
                }],
                yAxes: [{
                    gridLines: {
                        display:true
                    }   
                }]
            },
            title: {
                display: true,
                text: 'Samlede materialemængder [m3]',
                fontSize: 20,
                fontColor: 'DarkSlateGrey',
                padding: 15,
               }
          }
        //#endregion
         //#region 
        const chartData2 = this.getData(mergedPassMaterial)
        const chartLabels2 = this.getLabels(mergedPassMaterial)

        const data2 = {
           labels: chartLabels2,
           datasets: [
             {
               label: '',
               data: chartData2,
               backgroundColor: [
                   'rgba(70, 200, 235, 1)',
                   'rgba(30, 132, 235, 1)',
                   'rgba(50, 170, 150, 1)',
                   'rgba(24, 200, 235, 1)',
                   'rgba(54, 162, 235, 1)',
                   'rgba(100, 162, 235, 1)',
                   'rgba(120, 152, 205, 1)',
                   'rgba(50, 255, 205, 1)',
                   'rgba(50, 105, 255, 1)',
                   'rgba(70, 125, 235, 1)',
               ],
             },
           ],
         }
         
         const options2 = {
           maintainAspectRatio: false, 
           legend: {
               display: false
           },
           scales: {
               xAxes: [{
                   gridLines: {
                       display:false
                   }
               }],
               yAxes: [{
                   gridLines: {
                       display:true
                   }   
               }]
           },
           title: {
               display: true,
               text: 'Samlede råmaterialemængder [%]',
               fontSize: 20,
               fontColor: 'DarkSlateGrey',
               padding: 15,
              }
         }
       //#endregion
        return(
            <div>
                <h1 className='ResultHeader'>Materialemængder</h1>
                <div className='ResultContainer'>
                    <div className='aResult'>
                        <p>Ydervægge</p>
                        {wallMaterialAmount}
                    </div>
                    <div className='aResult'>
                        <p>Tag</p>
                        {roofMaterialAmount}
                    </div>
                    <div className='aResult'>
                        <p>Gulve</p>
                        {floorMaterialAmount}
                    </div>
                    <div className='aResult'>
                        <p>Fundament</p>
                        {foundationMaterialAmount}
                    </div>
                    <div className='aResult' id='NoBorder'>
                        <p>Indervægge</p>
                        {inWallMaterialAmount}
                    </div>
                </div>
                <div className='SumContainer'>
                    <div className='SumResult'>
                        <p>Samlet</p>
                        {mergedMaterialAmount}
                    </div>
                    <div className='BarContainer'>
                        <Bar data={data} options={options} width={620} height={620}/>
                    </div>
                    <div className='SumResult'>
                        <p>Samlede råmaterialemængder</p>
                        {mergedPassMaterialAmount}
                    </div>
                    <div className='BarContainer'>
                        <Bar data={data2} options={options2} width={620} height={620}/>
                    </div>
                </div>
                <Impacts materials={this.props.materials} building={this.props.building} amount={mergedMaterials}/>
                <Download materials={this.props.materials} amount={mergedMaterials}/>
            </div>
        )
    }

}

export default Results