import './Download.css'
import React from 'react'
import JSZip from 'jszip'
import saveAs from 'file-saver'

const BetonMatPas = require("../../materialepas/beton.json")
const GipsMatPas = require("../../materialepas/gips.json")
const IsoleringMatPas = require("../../materialepas/isolering.json")
const MetalMatPas = require("../../materialepas/metal.json")
const MurstenMatPas = require("../../materialepas/mursten.json")
const PapMatPas = require("../../materialepas/pap.json")
const SkiferMatPas = require("../../materialepas/skifer.json")
const StenMatPas = require("../../materialepas/sten.json")
const TeglMatPas = require("../../materialepas/tegl.json")
const TræMatPas = require("../../materialepas/træ.json")


class Download extends React.Component {
    constructor(props) {
      super(props)

      this.downloadZip = this.downloadZip.bind(this)
      this.removeMissing = this.removeMissing.bind(this)

      }    

      removeMissing(array) {
        const newArray = []
        for (var x of array) {
            if (x[1] !== 0) {
                newArray.push(x)
            }
        } return newArray
    }
    
      downloadZip() {
        const BygPas = {Bygningsdele: [{Væg: this.props.materials.wall},
            {Tag: this.props.materials.roof},
            {Gulv: this.props.materials.floor},
            {Fundament: this.props.materials.foundation},
            {Indervæg: this.props.materials.inwall}],
            Mængder: this.props.amount}

        var zip = new JSZip();

        for (var x of this.removeMissing(this.props.amount)) {
            zip.file(`${x[0]}.json`, JSON.stringify(eval(`${x[0]}`+'MatPas'), null, 2))
        }

        zip.file('Bygningspas.json', JSON.stringify(BygPas, null, 2))

        zip.generateAsync({type:"blob"}).then(function(content) {
            // see FileSaver.js
            saveAs(content, "Bygningspas.zip");
        });
    }

    
    render() {
      return (
        <a className='buttonDownload' onClick={this.downloadZip}>Download</a>
        )
    }
  }

  export default Download